import { buziAPI } from '@/api';
export default {
	data() {
		return {
			userName: '',
			password: '',
			error: false,
			errorMessage: ''
		};
	},
	watch: {},
	methods: {
		login() {
			let that = this;
			if (!that.userName) {
				that.error = true;
				that.errorMessage = '用户名不能为空';
			} else if (!that.password) {
				that.error = true;
				that.errorMessage = '密码不能为空';
			} else {
				buziAPI.getUserLogin(
					{
						username: that.userName,
						password: that.password,
						savestate: 1,
						r: 'https://m.weibo.cn/',
						ec: 0,
						pagerefer: 'https://m.weibo.cn/login?backURL=https%253A%252F%252Fm.weibo.cn%252F',
						entry: 'mweibo',
						wentry: '',
						loginfrom: '',
						client_id: '',
						code: '',
						qq: '',
						mainpageflag: 1,
						hff: ''
					},
					res => {}
				);
			}
		}
	}
};
